
.treemap-content {
  max-width: 1200px;
  margin: auto;
}
.controls {
  padding: 10px 0;
}
.controls .filters {
    float: right;
}
.controls .filters .filter {
      float: left;
      padding-left: 10px;
}
.controls .hierarchies {
    float: left;
}
.controls:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.treemap {
  min-height: 500px;
  overflow: hidden;
}
.node {
  display: block;
  border: 0;
  color: #fff;
  overflow: hidden;
  position: absolute;
  outline: 1px solid #fff;
  font-size: 0.8em;
  text-indent: -1000px;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 5;
  font-weight: 300;
  text-shadow: -2px -2px 13px rgba(150, 150, 150, 0.9);
}
.node a, .node a:hover {
    color: #fff;
    text-decoration: none;
}
.node.big {
    text-indent: 2px;
}
.node.big .amount {
      color: #fff;
      display: block;
      padding-bottom: 0.2em;
      font-size: 1.5em;
      text-indent: 2px;
}
.node.big, .node.big:hover {
      color: #fff;
      text-decoration: none;
}
.node:hover {
    text-indent: 2px;
    text-decoration: none;
}
.table {
  width: 100%;
  margin-top: 1em;
}
.table td, .table th {
    font-size: 0.9em;
}
.table td.num, .table th.num {
    text-align: right;
}
.table .hide-small, .table .small {
    display: none;
}
.table .label a, .table .label a:hover {
    color: #fff;
    text-decoration: none;
    font-weight: 400;
}
@font-face {
  font-family: 'fa-minimal';
  src: url("../assets/fonts/fa-minimal.eot?xss5ew");
  src: url("../assets/fonts/fa-minimal.eot?xss5ew#iefix") format("embedded-opentype"), url("../assets/fonts/fa-minimal.ttf?xss5ew") format("truetype"), url("../assets/fonts/fa-minimal.woff?xss5ew") format("woff"), url("../assets/fonts/fa-minimal.svg?xss5ew#fa-minimal") format("svg");
  font-weight: normal;
  font-style: normal;
}
.fa {
  display: inline-block;
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'fa-minimal' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fa-check:before {
  content: "\f00c";
}
.fa-info-circle:before {
  content: "\f05a";
}
.fa-square:before {
  content: "\f0c8";
}
.fa-plus-square:before {
  content: "\f0fe";
}
.fa-minus-square:before {
  content: "\f146";
}
.fa-level-up:before {
  content: "\f148";
}



